/* eslint-disable prefer-promise-reject-errors */
/* eslint-disable react/display-name */
/* eslint-disable react/destructuring-assignment */
import React, {useState, useEffect} from 'react';
import { Row } from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import useRequest from '../../../hooks/use-request';
import { log } from '../../../helpers/Utils';
import AppError from '../../../helpers/AppError';

const Results = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please Provide Hackathon ID');

  const [data, setData1] = useState({});
  const [isLoaded, setIsLoaded] = useState(false);

  const [doRequest, requestLoading, requestError] = useRequest({
    url: `/getdetails/${id}`,
    method: 'GET',

    nextSuccess: (responseData) => {
      setData1(responseData.getdetails);
      setIsLoaded(true);
    },
  });

  useEffect(() => {
    doRequest();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (requestLoading || !isLoaded) return <div className="loading" />;

  if (requestError) log(requestError);

  console.log(data);

  const style = {
    minWidth: '18rem',
  };

  const title = `${data.hackathon.name} Results`;

  return (
    <>
      <Row className="">
        <Colxx xxs="12">
          <Breadcrumb heading={title} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      {data.hackathon_winner && (
        <Row>
          <div className="d-flex overflow-auto">
            {data.hackathon_winner.map((item) => {
              return (
                <div
                  className="card text-white bg-info mb-3 mr-3 pt-3"
                  style={style}
                  key="1"
                >
                  <div className="card-header display-4">{item.position}</div>
                  <div className="card-body pt-1">
                    <h5 className="card-title">{item.full_name}</h5>
                    <p className="card-text">
                        has been awarded the {item.position}st position for their splendid participation and contribution.
                    </p>
                  </div>
                </div>
              );
            })}
          </div>
        </Row>
      )}
    </>
  );
};

export default Results;
